import React, { Component } from 'react';
import './App.css';
import ReactMarkdown from 'react-markdown'
import {DataTable} from 'dashboard-reactjs-components';

class App extends Component {
    constructor(){
        super();
        this.state = {
            dataTop: '# DataTable\n' +
            '**A ReactJS component to render a datatable.**\n' +
            'The component comes with built-in styles.\n' +
            '### Installation\n' +
            'Install DataTable with npm:\n' +
            '```sh\n' +
            '$ npm install --save dashboard-components\n' +
            '```\n' +
            '\n' +
            '### Usage\n' +
            'Very easy to use. Just provide props with total amount of things that you want to display on the page.\n' +
            '\n' +
            '```\n' +
            'import React, { Component } from "react";\n' +
            'import ReactDOM from "react-dom";\n' +
            'import {DataTable} from "dashboard-components";\n' +
            '\n' +
            'class App extends Component {\n' +
            '  constructor(props) {\n' +
            '    super(props);\n' +
            '    this.state = {\n' +
            '      data=[\n' +
            '      {name:\'Bob\', age:24, location: \'America\'},\n' +
            '      {name:\'Jack\', age:32, location: \'Australia\'},\n' +
            '      {name:\'Daniel\', age:45, location: \'Dubai\'},\n' +
            '      {name:\'Harry\', age:37, location: \'Canada\'},\n' +
            '      {name:\'David\', age:25, location: \'Africa\'},\n' +
            '      {name:\'Kim Johnson\', age:34, location: \'Paris\'}\n' +
            '      ],\n' +
            '      headerList: [\n' +
            '      {label:\'Name\',value: \'name\', sort: true, style: {minWidth:\'75px\'}},\n' +
            '      {label:\'Age\',value: \'age\', sort: true,style: {minWidth:\'95px\'}},\n' +
            '      {label:\'Location\',value: \'location\', sort: true, style: {minWidth:\'95px\'}}\n' +
            '      ]\n' +
            '    };\n' +
            '  }\n' +
            '\n' +
            '  handleEdit(obj){\n' +
            '    console.log(obj,\' handleEdit\')\n' +
            '  }\n' +
            '  handleDelete(obj){\n' +
            '    console.log(obj,\' handleDelete\')\n' +
            '  }\n' +
            '\n' +
            '  render() {\n' +
            '    return (\n' +
            '      <div>\n' +
            '        <DataTable  list={this.state.data}\n' +
            '                    headerList={this.state.headerList}\n' +
            '                    actionField={true}\n' +
            '                    actionsList={[\n' +
            '                        {icon:\'edit\',handleAction: this.handleEdit},\n' +
            '                        {icon:\'delete\',handleAction: this.handleDelete}\n' +
            '                    ]}/>\n' +
            '      </div>\n' +
            '    );\n' +
            '  }\n' +
            '}\n' +
            '\n' +
            'ReactDOM.render(<App />, document.getElementById("root"));\n' +
            '\n' +
            '```\n' ,
            dataBottom: '\n' +
            '### Params\n' +
            '\n' +
            '| Name | Type | Default | Description |\n' +
            '| --- | --- | :---: | --- |\n' +
            '| showTotalValue | String |    | Total count of items which you are going to display\n' +
            '| list | Array |    | **Required.** List of the Item which you are going to display\n' +
            '| actionField | Boolean  | False | Adds Actions buttons field in table.\n' +
            '| actionsList  | Array |    | Contains total actions your want to perform with Action Field(contains Icon(**Required.**) and EventHandler Function)\n' +
            '| search | Boolean | True | Show Search Filter on Top of the Table\n' +
            '| placeholder | String | Search here... | Placeholder of Search Input Field\n' +
            '| pagination | Boolean | True | Show Pagination on the bottom of the Datatable\n' +
            '| perPage | Number | 10 | Count of items per page\n' +
            '| defaultActivePage | Number | 1 | Default Active Page\n' +
            '| pageRangeDisplayed | Number | 3 | Range of pages in paginator, exclude navigation blocks (prev, next, first, last pages)\n' +
            '| sort | Boolean | True | Sort by any of the list item\n' +
            '| headerList | Array |   | Table Header List(Contains the fields you want to show in your datatable)\n' +
            '| serialNumber | Boolean | True | Shows the Sno. field in header\n' +
            '| noDataMessage | String | No Data Found. | When Data Table is empty this message will be displayed.\n' +
            '\n' +
            '### Using the icons\n' +
            'This project uses [Google Material Icons](https://material.io/tools/icons/). You have to link the script in your html file to display the icons.\n' +
            '```\n' +
            '<link href="https://fonts.googleapis.com/icon?family=Material+Icons" rel="stylesheet">\n' +
            '```\n' +
            'Read more in the [font portion](https://google.github.io/material-design-icons/#icon-font-for-the-web) of google developer guide.\n' +
            '\n' +
            '### Pagination\n' +
            '\n' +
            'This component uses [react-js-pagination](https://www.npmjs.com/package/react-js-pagination).\n',
            data:[
                {name:'Bob', age:24, location: 'America'},
                {name:'Jack', age:32, location: 'Australia'},
                {name:'Daniel', age:45, location: 'Dubai'},
                {name:'Harry', age:37, location: 'Canada'},
                {name:'David', age:25, location: 'Africa'},
                {name:'Kim Johnson', age:34, location: 'Paris'}
            ],
            headerList: [
                {label:'Name',value: 'name', sort: true, style: {minWidth:'75px'}},
                {label:'Age',value: 'age', sort: true, style: {minWidth:'95px'}},
                {label:'Location',value: 'location', sort: true, style: {minWidth:'95px'}}
            ]
        }
    }

    handleDelete(obj){
        console.log(obj, ' handleDelete')
    }

    handleEdit(obj){
        console.log(obj, ' handleEdit')
    }

  render() {
    return (
      <div className="App">
        <header className="App-header">
            <div className="App-title">
                <a href="https://github.com/AmirKhanOfficial/dashboard-components" target="_blank" rel="noopener noreferrer" >dashboard-components</a>
            </div>
        </header>
        <div className="App-intro">
            <ReactMarkdown source={this.state.dataTop} />
            <DataTable
                list={this.state.data}
                headerList={this.state.headerList}
                actionField={true}
                actionsList={[
                    {icon:'edit',handleAction: this.handleEdit},
                    {icon:'delete',handleAction: this.handleDelete}
                ]}
            />
            <ReactMarkdown source={this.state.dataBottom} />
        </div>
      </div>
    );
  }
}

export default App;



